import {getAuthUserData} from "./auth-reducer";
import {getAllQuestions} from "./question-reducer";
import {appAPI, profileAPI} from "../../api/api";
import {stopSubmit} from "redux-form";

const SET_FORM_MESSAGE = '/build-the-world/app/SET_FORM_MESSAGE';
const SET_NOTIF = '/build-the-world/app/SET_NOTIF';

const initialState = {
    messageAuthForm: '',
    notifications: []
};

const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_FORM_MESSAGE:
            return {
                ...state,
                messageAuthForm: action.messageAuthForm
            }
        case SET_NOTIF:
            return {
                ...state,
                notifications: [...action.notifications]
            }
        default: {
            return state;
        }
    }
}

export const initializedSuccess = (messageAuthForm) => ({type: SET_FORM_MESSAGE, messageAuthForm});
export const setNotif = (notifications) => ({type: SET_NOTIF, notifications});

export const setMessageForm = () => (dispatch) => {
    let date = new Date().getHours();
    if (date >= 0 && date <= 4) date = ' !';
    if (date >= 5 && date <= 12) date = ' !';
    if (date >= 13 && date <= 17) date = ' !';
    if (date >= 18 && date <= 23) date = ' !';
    dispatch(initializedSuccess(date));
}

export const getNotifications = (userId) => async (dispatch) => {
    const response = await appAPI.getnotif(userId);
    if (response.data.resultCode === 1) {
        dispatch(setNotif(response.data.data));
    } else {
        dispatch(stopSubmit("setEmail", {_error: 'error'}));
    }
}

export const readNotification = (userId, notifId) => async (dispatch) => {
    const response = await appAPI.readnotif(userId, notifId);
    if (response.data.resultCode === 1) {
        dispatch(getNotifications(userId));
    } else {
        dispatch(stopSubmit("setEmail", {_error: 'error'}));
    }
}

export const initializeApp = (history, ability) => (dispatch) => {
    dispatch(getAllQuestions());
    dispatch(getAuthUserData(history, ability));
    dispatch(setMessageForm());
}

export default appReducer;