import {stopSubmit} from "redux-form";
import {questionAPI} from "../../api/api";

const SET_QUESTIONS = '/build-the-world/question/SET_QUESTIONS';
const SET_ANSWERS = '/build-the-world/question/SET_ANSWERS';
const SET_DEFAULT_ANSWERS = '/build-the-world/question/SET_DEFAULT_ANSWERS';
const SET_ALL_ANSWERS = '/build-the-world/question/SET_ALL_ANSWERS';

const initialState = {
    questions: [],
    currQuestion: 0,
    answers: [],
    userAnswers: []
};

const questionReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_QUESTIONS: {
            return {
                ...state,
                questions: action.payload
            };
        }
        case SET_ANSWERS: {
            return {
                ...state,
                answers: [...state.answers, action.payload.answers],
                currQuestion: action.payload.currQuestion
            };
        }
        case SET_DEFAULT_ANSWERS: {
            return {
                ...state,
                questions: action.payload,
                answers: [],
                currQuestion: 0
            };
        }
        case SET_ALL_ANSWERS: {
            return {
                ...state,
                userAnswers: action.payload
            };
        }
        default: {
            return state;
        }
    }
};

export const setQuestions = (questions) => ({type: SET_QUESTIONS, payload: questions});
export const setAnswers = (answers, currQuestion) => ({type: SET_ANSWERS, payload: {answers, currQuestion}});
export const setAllAnswers = (answers) => ({type: SET_ALL_ANSWERS, payload: answers});
export const setDefaultAnswers = (questions) => ({type: SET_DEFAULT_ANSWERS, payload: questions});

export const getAllQuestions = () => async (dispatch) => {
    const response = await questionAPI.getquestions();
    if (response.data.resultCode === 1) {
        dispatch(setQuestions(response.data.data));
    } else {
        dispatch(stopSubmit("getallquestions", {_error: response.data.message}));
    }
}

export const pullAnswer = (answer = null, currQuestion = 0) => async (dispatch) => {
    if (answer === null) {
        const response = await questionAPI.getquestions();
        if (response.data.resultCode === 1) {
            dispatch(setDefaultAnswers(response.data.data));
        } else {
            dispatch(stopSubmit("getallquestions", {_error: response.data.message}));
        }
    } else {
        dispatch(setAnswers(answer, currQuestion));
    }
}

export const getAllAnswers = (userId, history) => async (dispatch) => {
    const response = await questionAPI.getallanswers(userId);
    if (response.data.resultCode === 1) {
        dispatch(setAllAnswers(response.data.data));
    } else {
        dispatch(stopSubmit("getallquestions", {_error: response.data.message}));
        history.push('/');
    }
}

export default questionReducer;