/**
 *     
 *
 * var rn = new RussianName('  ');
 * rn.fullName(rn.gcaseRod); //   
 *
 *     .   .
 *
 * ,      . .
 *
 * @version  0.1.6
 * @author   Johnny Woo <agalkin@agalkin.ru>
 */
const RussianNameProcessor = {
    sexM: 'm',
    sexF: 'f',
    gcaseIm: 'nominative', gcaseNom: 'nominative',      // 
    gcaseRod: 'genitive', gcaseGen: 'genitive',        // 
    gcaseDat: 'dative',                                       // 
    gcaseVin: 'accusative', gcaseAcc: 'accusative',      // 
    gcaseTvor: 'instrumentative', gcaseIns: 'instrumentative', // 
    gcasePred: 'prepositional', gcasePos: 'prepositional',   // 

    rules: {
        lastName: {
            exceptions: [
                '	,,,,,,, . . . . .',
                '	,,,,,,,,, . . . . .',
                '	,,, - - - - -'
            ],
            suffixes: [
                'f	,,,,,,,,,,,,,,,,,,,,,, . . . . .',
                'f	,  - - - - -',
                'f	       -- -- -- -- --',
                '	     -- -- -- -- --',
                'f	       - - - - -',

                '	 - - - - -',
                '	   - - - - -',
                '	   - - - - -',

                '	      ',

                '	                      . . . . .',
                '	,,,,,,, . . . . .',
                '	,                   . . . . .',
                '	,,,,,,           . . . . .',

                '	,            - - - - -',
                '	,,,,,  - - - - -',
                '	  - - - - -',
                '	   - - - - -',

                '	   - - - - -',

                '	  - - - - -',
                '	   - - - - -',
                '	  - - - - -',

                '	,,       ',

                '	,  -- -- -- -- --',
                '	,  -- -- -- -- --',

                '	,,,       ',

                '	  - - - - -',
                '	,  - - - -- -',
                '	  - - - -- -',
                '	,       ',

                '	,,,  -- -- -- - --',
                '	,   -- -- -- - --',
                '	       - - - - -',

                '	  -- -- -- -- --',
                '	  -- -- -- -- --',

                '	,       ',
                '	,,,,,,,,,,,,,       '
            ]
        },
        firstName: {
            exceptions: [
                '	    -- -- -- -- --',
                '	  --  --  --  --  --',
                'm	   . . . . .',
                'm	   --- --- --- --- ---',
                'f	,,,,   . . . . .'
            ],
            suffixes: [
                '	,,,,,,,   . . . . .',
                'f	,,,,,,,,,,,,,,,,,,,,,   . . . . .',

                'f	   - - .  -',
                'm	   - - - - -',

                '	,,,,,  - - - - -',
                '	  - - - - -',
                '	   - - - - -',
                '	  - - - - -',
                '	   - - - - -',
                '	  - - - - -',
                '	  - - - - -',
                '	   - - - - -',
                '	,,,,,,,,,,,,,,,,,	     '
            ]
        },
        middleName: {
            suffixes: [
                '	           ',
                '	  - - - - -'
            ]
        }
    },

    initialized: false,
    init() {
        if (this.initialized) return;
        this.prepareRules();
        this.initialized = true;
    },

    prepareRules() {
        for (const type in this.rules) {
            for (const key in this.rules[type]) {
                let i = 0
                const n = this.rules[type][key].length;
                for (; i < n; i++) {
                    this.rules[type][key][i] = this.rule(this.rules[type][key][i]);
                }
            }
        }
    },

    rule(rule) {
        const m = rule.match(/^\s*([fm]?)\s*(\S+)\s+(\S+)\s+(\S+)\s+(\S+)\s+(\S+)\s+(\S+)\s*$/);
        if (m) return {
            sex: m[1],
            test: m[2].split(','),
            mods: [m[3], m[4], m[5], m[6], m[7]]
        }
        return false;
    },

    //        
    word(word, sex, wordType, gcase) {
        let pick;
//      
        if (gcase === this.gcaseNom) return word;

        //  
        if (word.match(/[-]/)) {
            const list = word.split('-');
            let i = 0;
            const n = list.length;
            for (; i < n; i++) {
                list[i] = this.word(list[i], sex, wordType, gcase);
            }
            return list.join('-');
        }

        //  . .
        if (word.match(/^[-]\.?$/i)) return word;

        this.init();
        const rules = this.rules[wordType];

        if (rules.exceptions) {
            pick = this.pick(word, sex, gcase, rules.exceptions, true);
            if (pick) return pick;
        }
        pick = this.pick(word, sex, gcase, rules.suffixes, false);
        return pick || word;
    },

    //        
    pick(word, sex, gcase, rules, matchWholeWord) {
        const wordLower = word.toLowerCase();
        for (let i = 0, n = rules.length; i < n; i++) {
            if (this.ruleMatch(wordLower, sex, rules[i], matchWholeWord)) {
                return this.applyMod(word, gcase, rules[i]);
            }
        }
        return false;
    },

    // ,     
    ruleMatch(word, sex, rule, matchWholeWord) {
        if (rule.sex === this.sexM && sex === this.sexF) return false; // male by default
        if (rule.sex === this.sexF && sex !== this.sexF) return false;
        let i = 0;
        const n = rule.test.length;
        for (; i < n; i++) {
            const test = matchWholeWord ? word : word.substr(Math.max(word.length - rule.test[i].length, 0));
            if (test === rule.test[i]) return true;
        }
        return false;
    },

    //   ( )
    applyMod(word, gcase, rule) {
        let mod;
        switch (gcase) {
            case this.gcaseNom:
                mod = '.';
                break;
            case this.gcaseGen:
                mod = rule.mods[0];
                break;
            case this.gcaseDat:
                mod = rule.mods[1];
                break;
            case this.gcaseAcc:
                mod = rule.mods[2];
                break;
            case this.gcaseIns:
                mod = rule.mods[3];
                break;
            case this.gcasePos:
                mod = rule.mods[4];
                break;
            default:
                throw "Unknown grammatic case: " + gcase;
        }
        for (let i = 0, n = mod.length; i < n; i++) {
            const c = mod.substr(i, 1);
            switch (c) {
                case '.':
                    break;
                case '-':
                    word = word.substr(0, word.length - 1);
                    break;
                default:
                    word += c;
            }
        }
        return word;
    }
};

// new RussianName('  ')      //   
// new RussianName('  ')      //    
// new RussianName('', '')        //    
// new RussianName('', '', '', 'f') //     ('m'  'f')
export const RussianName = function (lastName, firstName, middleName, sex) {
    if (typeof firstName === 'undefined') {
        const m = lastName.match(/^\s*(\S+)(\s+(\S+)(\s+(\S+))?)?\s*$/);
        if (!m) throw "Cannot parse supplied name";
        if (m[5] && m[3].match(/(|)$/) && !m[5].match(/(|)$/)) {
            //   
            lastName = m[5];
            firstName = m[1];
            middleName = m[3];
            this.fullNameSurnameLast = true;
        } else {
            //   
            lastName = m[1];
            firstName = m[3];
            middleName = m[5];
        }
    }
    this.ln = lastName;
    this.fn = firstName || '';
    this.mn = middleName || '';
    this.sex = sex || this.getSex();
};
RussianName.prototype = {
    // constants
    sexM: RussianNameProcessor.sexM,
    sexF: RussianNameProcessor.sexF,
    gcaseIm: RussianNameProcessor.gcaseIm, gcaseNom: RussianNameProcessor.gcaseNom, // 
    gcaseRod: RussianNameProcessor.gcaseRod, gcaseGen: RussianNameProcessor.gcaseGen, // 
    gcaseDat: RussianNameProcessor.gcaseDat,                                           // 
    gcaseVin: RussianNameProcessor.gcaseVin, gcaseAcc: RussianNameProcessor.gcaseAcc, // 
    gcaseTvor: RussianNameProcessor.gcaseTvor, gcaseIns: RussianNameProcessor.gcaseIns, // 
    gcasePred: RussianNameProcessor.gcasePred, gcasePos: RussianNameProcessor.gcasePos, // 

    fullNameSurnameLast: false,

    ln: '', fn: '', mn: '', sex: '',
    //     ,      
    getSex() {
        if (this.mn.length > 2) {
            switch (this.mn.substr(this.mn.length - 2)) {
                case '':
                    return this.sexM;
                case '':
                    return this.sexF;
            }
        }
        return '';
    },

    fullName(gcase) {
        return (
            (this.fullNameSurnameLast ? '' : this.lastName(gcase) + ' ')
            + this.firstName(gcase) + ' ' + this.middleName(gcase) +
            (this.fullNameSurnameLast ? ' ' + this.lastName(gcase) : '')
        ).replace(/^ +| +$/g, '');
    },
    lastName(gcase) {
        return RussianNameProcessor.word(this.ln, this.sex, 'lastName', gcase);
    },
    firstName(gcase) {
        return RussianNameProcessor.word(this.fn, this.sex, 'firstName', gcase);
    },
    middleName(gcase) {
        return RussianNameProcessor.word(this.mn, this.sex, 'middleName', gcase);
    }
}