import {profileAPI, reviewAPI} from "../../api/api";
import {stopSubmit} from "redux-form";
import {RussianName} from "../../utils/name";
import withReactContent from "sweetalert2-react-content";
import Swal from "sweetalert2";
const swal = withReactContent(Swal);

const SET_USER_PROFILE = '/build-the-world/profile/SET_USER_PROFILE';
const SET_UPLOAD = '/build-the-world/profile/SET_UPLOAD';
const SET_REVIEW = '/build-the-world/profile/SET_REVIEW';
const SET_COMMENT = '/build-the-world/profile/SET_COMMENT';
const SET_REWARDS = '/build-the-world/profile/SET_REWARDS';

const initialState = {
    profile: {
        name: null,
        phone: null,
        social: null,
        school: null,
        classRoom: null,
        city: null,
        teacher: null,
        teacherPosition: null,
        email: null,
        regDate: null,
        endDate: null,
        percent: null,
        isUpload: null
    },
    sex: null,
    certificateName: null,
    isReview: false,
    creativeComment: null,
    rewards: null
};

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_PROFILE: {
            return {
                ...state,
                profile: {...action.payload},
                sex: action.sex,
                certificateName: action.certificateName,
                isReview: action.isReview
            };
        }
        case SET_UPLOAD: {
            return {
                ...state,
                profile: {...state.profile, isUpload: action.payload}
            };
        }
        case SET_REVIEW: {
            return {
                ...state,
                isReview: action.payload
            };
        }
        case SET_COMMENT: {
            return {
                ...state,
                creativeComment: action.comment
            };
        }
        case SET_REWARDS: {
            return {
                ...state,
                rewards: action.rewards
            };
        }
        default: {
            return state;
        }
    }
};

export const setUserProfile = (name, phone, social, school, classRoom, city, teacher, teacherPosition, email, regDate, endDate, percent, isUpload, isReview, sex, certificateName) => ({
    type: SET_USER_PROFILE,
    payload: {
        name,
        phone,
        social,
        school,
        classRoom,
        city,
        teacher,
        teacherPosition,
        email,
        regDate,
        endDate,
        percent,
        isUpload
    },
    sex,
    certificateName,
    isReview
});

export const setUpload = (upload) => ({type: SET_UPLOAD, payload: upload});
export const setReview = (reviewStatus) => ({type: SET_REVIEW, payload: reviewStatus});
export const setCreative = (comment) => ({type: SET_COMMENT, comment});
export const setRewards = (rewards) => ({type: SET_REWARDS, rewards});

export const getUserProfile = (userId) => async (dispatch) => {
    const response = await profileAPI.getProfile(userId);
    if (response.data.resultCode) {
        const {
            user_name, user_phone,
            user_social, user_school,
            user_class, user_city,
            user_teacher, user_teacherPosition,
            user_email, user_reg_date, user_endtest_date,
            user_percent, isUpload_doc, isReview
        } = response.data.data;
        const sex = new RussianName(user_name);
        const certificateName = sex.fullName(sex.gcaseDat);
        dispatch(setUserProfile(
            user_name, user_phone,
            user_social, user_school,
            user_class, user_city,
            user_teacher, user_teacherPosition,
            user_email, user_reg_date, user_endtest_date,
            user_percent, isUpload_doc, isReview, sex.sex, certificateName
        ));
    } else {
        const message = response.data.message;
        dispatch(stopSubmit("getProfile", {_error: message}));
    }
}

export const setUserEmail = (userId, email) => async (dispatch) => {
    const response = await profileAPI.setEmail(userId, email);
    if (response.data.resultCode === 1) {
        await swal.fire({
            title: "!",
            text: "     !",
            icon: "success",
            confirmButtonText: "OK"
        }).then(() => {
            dispatch(getUserProfile(userId));
        });
    } else {
        const message = response.data.message;
        await swal.fire({
            title: "!",
            text: message,
            icon: "error",
            confirmButtonText: "OK"
        }).then(() => {
            dispatch(stopSubmit("setEmail", {_error: message}));
        });
    }
}

export const createCertificate = (nameCertificate, userName, login, type) => async (dispatch) => {
    const response = await profileAPI.createcertificate(nameCertificate, userName, login, type);
    if (response.data.resultCode === 1) {

    } else {
        dispatch(stopSubmit("createCertificate", {_error: 'error'}));
    }
}

export const setEndTest = (answers, userId, userNameCertificate, userName, login) => async (dispatch) => {
    const response = await profileAPI.setendtest(answers, userId);
    const message = response.data.message;
    if (response.data.resultCode === 1) {
        dispatch(getUserProfile(userId));
        const percent = response.data.percent;
        let type;
        if (percent >= 50 && percent < 65) {
            type = 'III';
        } else if (percent >= 65 && percent < 80) {
            type = 'II';
        } else if (percent >= 80) {
            type = 'I';
        }
        dispatch(createCertificate(userNameCertificate, userName, login, type));
        await swal.fire({
            title: "!",
            text: '    ',
            icon: "success",
            confirmButtonText: ""
        });
    } else {
        await swal.fire({
            title: "!",
            text: message,
            icon: "error",
            confirmButtonText: "OK"
        }).then(() => {
            dispatch(stopSubmit("setEndTest", {_error: message}));
        });
    }
}

export const uploadDoc = (file, setLoad) => async (dispatch) => {
    const formData = new FormData();
    formData.append("document", file);
    const response = await profileAPI.uploaddoc(formData);
    if (response.data.resultCode === 1) {
        setLoad(0);
        await swal.fire({
            title: "!",
            text: "  !  .",
            icon: "success",
            confirmButtonText: "OK"
        }).then(() => {
            dispatch(setUpload(1));
        });
    } else {
        dispatch(stopSubmit("uploadDoc", {_error: 'error'}));
    }
}

export const saveReview = (reviews, userId) => async (dispatch) => {
    const response = await reviewAPI.setreview(reviews, userId);
    if (response.data.resultCode === 1) {
        await swal.fire({
            title: "!",
            text: "  ,      .",
            icon: "success",
            confirmButtonText: "OK"
        })
        dispatch(setReview(true));
    } else {
        dispatch(stopSubmit("saveReview", {_error: 'error'}));
    }
}

export const getCreativeComment = (userId) => async (dispatch) => {
    const response = await profileAPI.getcreative(userId);
    if (response.data.resultCode === 1) {
        dispatch(setCreative(response.data.data));
    } else {
        dispatch(stopSubmit("getCreativeComment", {_error: 'error'}));
    }
}

export const getRewards = (userLogin) => async (dispatch) => {
    const response = await profileAPI.getrewards(userLogin);
    if (response.data.resultCode === 1) {
        dispatch(setRewards(response.data.assets));
    } else {
        dispatch(stopSubmit("getRewards", {_error: 'error'}));
    }
}

export default profileReducer;