import * as axios from "axios";
import configApi from './config';

const instance = axios.create({
    baseURL: configApi.SERVER_URI,
    withCredentials: true
});

export const profileAPI = {
    getProfile(userId) {
        return instance.get(`/profile/getprofile/${userId}/`);
    },
    getcreative(userId) {
        return instance.post(`/profile/getcreative/`, userId);
    },
    setEmail(userId, email) {
        return instance.post(`/profile/setemail/`, {userId, email});
    },
    setendtest(answers, userId) {
        return instance.post(`/profile/setendtest/`, {answers, userId});
    },
    createcertificate(nameCertificate, userName, login, type) {
        return instance.post(`/profile/createcertificate/`, {nameCertificate, userName, login, type});
    },
    getrewards(userLogin) {
        return instance.post(`/profile/getrewards/`, {userLogin});
    },
    uploaddoc(file) {
        return instance.post('/profile/uploaddoc/', file, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        });
    }
}

export const reviewAPI = {
    setreview(reviews, userId) {
        return instance.post(`/review/setreview/`, {reviews, userId});
    }
}

export const appAPI = {
    getnotif(userId) {
        return instance.post(`/app/getnotif/`, userId);
    },
    readnotif(userId, notifId) {
        return instance.post(`/app/readnotif/`, {userId, notifId});
    }
}

export const authAPI = {
    me() {
        return instance.get(`/auth/me/`);
    },
    login(userObject) {
        return instance.post(`/auth/signin/`, userObject);
    },
    loginecoworld(userObject) {
        return instance.post(`/auth/signinecoworld/`, userObject);
    },
    setEcoId(userId) {
        return instance.post(`/auth/bindecoworld/`, userId);
    },
    register(userObject) {
        return instance.post(`/auth/signup/`, userObject);
    },
    logout() {
        return instance.get(`/auth/logout/`);
    },
    changePassword(userId, login, currPassword, newPassword) {
        return instance.post(`/auth/changepassword/`, {userId, login, currPassword, newPassword});
    }
}

export const questionAPI = {
    getquestions() {
        return instance.get(`/question/getallquestions/`);
    },
    getallanswers(userId) {
        return instance.post(`/question/getallanswers/`, userId);
    }
}
